*,
*:after,
*:before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
input,
button,
textarea,
select {
  font-size: 16px;
  font-family: $BASE-FONT;
}

ul {
  list-style: none;
}

a, button {
  background-color: transparent;
  border: none;
  outline: none;
  display: inline-flex;
  text-decoration: none;
}
body {
    display: flex;
    justify-content: center;
    font-size: $base-font-size;
    background-color: $BG-COLOR;
}
.container {
    max-width: 1170px;
    padding: 0 24px;
}